import axios from "axios";
import {
  GET_SECTIONA_REQUEST,
  GET_SECTIONA_SUCCESS,
  GET_SECTIONA_FAIL,
} from "../constants/appraisalConstants";

export const getAllSectionA = (id) => async (dispatch, getState) => {
  try {
    dispatch({ type: GET_SECTIONA_REQUEST });

    const {
      userLogin: { userInfo },
    } = getState();

    const config = {
      headers: {
        "Content-Type": "application/json",
        Authorization: `Bearer ${userInfo.token}`,
      },
    };
    const { data } = await axios.get(
      `/api/v1/staff/appraisal/sectiona/`,
      config
    );

    dispatch({
      type: GET_SECTIONA_SUCCESS,
      payload: data.data,
    });
  } catch (error) {
    dispatch({
      type: GET_SECTIONA_FAIL,
      payload:
        error.response && error.response.data.error
          ? error.response.data.error
          : error.message,
    });
  }
};
