import React, { useEffect, useState } from "react";
import Navigation from "../components/Navbar";
import TopNavbar from "../components/Navbar/TopNavbar";
import { Button } from "@chakra-ui/react";

import styles from "./styles.module.css";

import { useSelector, useDispatch } from "react-redux";
import { getAllSectionA } from "../actions/appraisalActions";

const SectionA = () => {
  const [index, setIndex] = useState(0);

  const dispatch = useDispatch();

  useEffect(() => {
    dispatch(getAllSectionA());
  }, [dispatch]);
  const getSectionA = useSelector((state) => state.getSectionA);
  const { loading, error, sectiona } = getSectionA;

  const handleNext = () => {
    const newIndex = index + 1;
    setIndex(newIndex);
  };

  const handleBack = () => {
    const newIndex = index - 1;
    setIndex(newIndex);
  };

  return (
    <div className={styles.app}>
      <div className={styles.appGrid}>
        <Navigation dashboard={styles.active} />
        <div className={styles.appContents}>
          <TopNavbar title="SectionA" />
          <div className={styles.contents}>
            <div className={styles.contentGrid}>
              <div className={styles.left}>
                {sectiona && sectiona[index].title}
                {index !== 0 && (
                  <Button
                    colorScheme="teal"
                    type="button"
                    onClick={handleBack}
                    className="btn btn-dark"
                  >
                    Back
                  </Button>
                )}

                <Button
                  colorScheme="teal"
                  type="button"
                  onClick={handleNext}
                  className="btn btn-success"
                >
                  Next
                </Button>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default SectionA;
